.card-wrapper {
    width: calc(50% - 27px);
    display: inline-block;
    border-radius: 2px;
    border: 1px solid rgba(68, 68, 68, 0.1);
    margin-right: 50px;
    margin-bottom: 60px;
}

.card-college-img-wrapper {
    position: relative;
    img {
        display: inline-block;
        width: 100%;
    }
    i {
        position: absolute;
        background-color: rgba(0,0,0,0.7);
        top: 0;
        bottom: 5px;
        left: 0;
        right: 0;
    }
}

.card-college-img-bottom {
    position: absolute;
    bottom: 0px;
    left: 0px;
    display: inline-block;
    width: 100%;
    span {
        background-color: white;
        font-size: 13px;
        color: black;
        padding: 8px 13px;
        border-radius: 16px;
        display: inline-block;
        margin-right: 7px;
        font-family: mediumfont;
        &:first-of-type {
            margin-left: 14px;
            margin-bottom: 15px;
        }
        &:last-of-type {
            margin-right: 0px;
        }
    }
    .college-rank {
        font-size: 12px;
        line-height: 13px;
        color: white;
        margin: 0px;
        font-family: mediumfont;
        display: inline-block;
        padding-right: 14px;
        padding-top: 10px;
        float: right;
    }
}

.odd-img {
    margin-right: 0px; 
}
